<template>
  <div class="index">
    <div v-if="recommends.length" class="slider-wrapper">
      <div class="slider-content">
        <slider ref="slider">
          <div v-for="item in recommends">
            <a :href="item.linkUrl">
              <!--<img @load="loadImage" :src="item.picUrl">-->
              <img :src="item.picUrl">
            </a>
          </div>
        </slider>
      </div>
    </div>
  </div>
</template>

<script>
  import {sliderItems, searchplace} from '../../server/api'
  import Slider from '../../base/slider/slider'
  export default {
    data() {
      return {
        recommends: [
          {
            linkUrl: 'http://y.qq.com/w/album.html?albummid=000zQSi93tJtgY',
            picUrl: 'http://y.gtimg.cn/music/common/upload/MUSIC_FOCUS/1075114.jpg'
          },
          {
            linkUrl: 'https://y.qq.com/m/act/TNTB/index.html?openinqqmusic=1&ADTAG=jiaodiantu',
            picUrl: 'http://y.gtimg.cn/music/common/upload/MUSIC_FOCUS/983421.jpg'
          }
        ]
      }
    },
    mounted() {
      sliderItems().then(res => {
        console.log(res)
        console.log(res)
        console.log(res)
      })
      searchplace('qqq', 'www').then(res => {
        console.log(res)
      })
    },
    methods: {},
    components: {
      Slider
    }
  }
</script>

<style lang="scss" scoped>
  .slider-wrapper {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 40%;
    overflow: hidden;
    .slider-content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

  }
</style>




